.priceTabelMainDiv{
    width: 49%;
    float: left;
    height: 400px;
}

.otherText{
    padding: auto;
    padding-top: 1px;
    font-size: 18px;
    text-align: center;
}


.labelText{
    font-size: x-large;
    font-weight: 800;
    text-align: center;
}

.thTyo{
    width: 70%;
}

@media screen and (max-width: 1050px) {

    .priceTabelMainDiv{
        width: 98%;
        height: 400px;
    }
    
    .otherText{
        padding: auto;
        padding-top: 1px;
        font-size: 16px;
        text-align: center;
    }
    
    
    .labelText{
        font-size: large;
        font-weight: 600;
        text-align: center;
    }
    
    .thTyo{
        width: 70%;
    }

}